.server-index-container {
    background-color: #202225;
    width: 82px;
    height: 100%;
    display: flex;
    flex-direction: column;
    user-select: none;
    overflow-y: auto;
    align-items: center;
}

.server-index-container::-webkit-scrollbar {
    display: none; 
}

.server > h3 {
    margin-bottom: 0px;
    margin-top: 10px;
    color: #DBDCDD;
    background-color: #36393F;
    text-align: center;
    width: 50px;
    height: 50px;
    line-height: 50px;
    letter-spacing: -2px;
    border-radius: 50px;
    font-weight: 400;
    overflow: hidden;
}

.server > h3:hover {
    transition: border-radius .2s;
    border-radius: 17px;
    background-color: #5865F2;
    color: white;
    cursor: pointer;
}

.direct-messages {
    background-image: url('../../assets/serversPage/discord-2-128.png');
    background-size: 30px;
    background-repeat: no-repeat;
    background-position: center;
}

.dm-border {
    margin-top: 10px;
    width: 50%;
    align-self: center;
    border-bottom: 2px solid #373A3F;
}

.add-server {
    color: #3BA55D !important;
    font-size: 40px !important;
    font-weight: 300 !important;
    line-height: 40px !important;
}

.add-server:hover {
    transition: border-radius .2s;
    border-radius: 17px;
    background-color: #3BA55D !important; 
    color: white !important; 
    cursor: pointer;
}

.unjoinedservers-list-container {
    width: 450px;
    height: 320px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
}

.server-unjoined-list-item {
    width: 350px;
    min-height: 60px;
    height: 60px;
    margin-top: 10px;
    background-color: #f0f3f6;
    border-radius: 10px;
    text-align: center;
    font-size: 20px;
    line-height: 60px;
    text-overflow: ellipsis;
}

.server-unjoined-list-item:hover {
    cursor: pointer;
    background-color: #e0e2e5;
}

.server-unjoined-list-item:active {
    background-color: #c7c8c9;
}